





































































































































































$bg: #2d3a4b;
$dark_gray: #889aa4;
$light_gray: #eee;
.login-container {
  position: fixed;
  height: 100%;
  width: 100%;
  // background-color: $bg;
  background: url('../../assets/img/bg.png') no-repeat center center;
  .login-form {
    position: absolute;
    left: 0;
    right: 0;
    width: 520px;
    max-width: 100%;
    padding: 35px 35px 15px 35px;
    margin: 120px auto;
  }
  .svg-container {
    padding: 6px 5px 6px 15px;
    color: $dark_gray;
    vertical-align: middle;
    width: 30px;
    display: inline-block;
  }
  .title {
    margin: 0px auto 40px auto;
    height: 86px;
    background: url('../../assets/img/login-logo.png') no-repeat center center;
  }
}
.footer-copy-right {
  width: 100%;
  line-height: 30px;
  position: absolute;
  bottom: 0;
  text-align: center;
}
