  cursor: pointer;
}
.btn-group {
  margin-bottom: 20px;
}
.pagination {
  float: right;
  margin: 10px -10px 10px 0;
}
.collapse-row {
  width: 100%;
  .collapse-progress {
    margin-top: 15px;
  }
}
.progress-item {
  height: 48px;
  line-height: 48px;
  .collapse-progress {
    margin-top: 15px;
    float: right;
  }
  .collapse-active {
    .el-progress-bar__inner:before {
      content: '';
      opacity: 0;
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      background: #fff;
      border-radius: 10px;
      animation: progress-active 2s ease-in-out infinite;
    }
  }
}
@keyframes progress-active {
  0% {
    opacity: 0.3;
    width: 0;
  }
  to {
    opacity: 0;
    width: 100%;
  }
}
.echarts {
  width: 300px;
  height: 200px;
}
