


































































































.s-menu {
  height: 100%;
  .el-menu--collapse {
    height: 100%;
    width: 80px;
    .s-pro-components-sider-menu-index-logo {
      padding-left: 22px;
    }
  }
  .el-menu-vertical-menu:not(.el-menu--collapse) {
    width: 256px;
    height: 100%;
  }
  .el-menu-item {
    background: #090a01;
  }
  .el-menu {
    border-right: none;
  }
  .el-submenu {
    .el-menu {
      background: #090a01;
    }
  }
  .is-active {
    background-color: #e17425 !important;
  }
  .icon-sidebar {
    background: url("../../assets/img/sidebar-icon.png") no-repeat left center;
    display: inline-block;
    width: 16px;
    height: 16px;
  }
  .el-menu--collapse {
    img {
      display: none;
    }
    .collapse-logo {
      display: block;
      margin-top: 13px;
    }
  }
}
.el-menu--vertical {
  .is-active {
    background-color: #e17425 !important;
  }
}
